import * as React from 'react';
import '../../App.css';
import ButtonCSS from '../Button2/ButtonCSS';
import ButtonCSSprimary from '../Button2/ButtonCSSprimary';
import './HeroSection.css';

// import HeroVideo from '../../assets/videos/paracosms_28sec_vid.mp4';
import HeroGif from '../../assets/images/paracosms_hero_gif_smaller.gif';

const COUNTDOWN_TO = 1640286000;

function secondsRemaining() {
    return Math.max(COUNTDOWN_TO - Date.now() / 1000, 0);
}

function withLeadingZero(num) {
    return num < 10 ? '0' + num : num + '';
}

function HeroSection() {
    const [timeRemainingSeconds, setTimeRemainingSeconds] = React.useState(secondsRemaining());

    React.useEffect(() => {
        const interval = setInterval(() => {
            setTimeRemainingSeconds(secondsRemaining());
        }, 1000);

        return () => {
            clearInterval(interval);
        };
    }, []);

    const hoursLeft = Math.floor(timeRemainingSeconds/(60*60));

    const minutesLeft = Math.floor((timeRemainingSeconds % (60*60)) / 60);

    const secondsLeft = Math.floor(timeRemainingSeconds % 60);

    var formattedTime = withLeadingZero(hoursLeft) + ' h : ' + withLeadingZero(minutesLeft) + ' m : ' + withLeadingZero(secondsLeft) + ' s';

    return (
        <div className='hero-container' id='home'>
            {/* <video className="hero-bg" src={HeroVideo} autoPlay loop muted /> */}
            <img className="hero-bg" src={HeroGif} alt="paracosms hero section"/>
            <h1>paracosms</h1>
            <p>(n.) detailed, prolonged imaginary worlds created by a child that often include human, animal, or alien creations.</p> 
            <div className="hero-small-caption">Artist Launchpad Pioneering the Avalanche Art Renaissance.</div> 

            <div className="countdown-timer-wrapper">
                <p>COUNTDOWN TO SALE:</p>
                <div className="countdown-timer" style={{color: '#fff'}}>{ formattedTime }</div>
            </div>
            <div className="hero-btns">
                <ButtonCSS className='btns'  
                buttonStyle='btn--outline'
                buttonSize='btn--large'
                >
                    LEARN MORE
                </ButtonCSS>

                <ButtonCSSprimary className='btns'  
                buttonStyle='btn--primary'
                buttonSize='btn--large'
                >
                    CLAIM YOUR REALM
                </ButtonCSSprimary>
            </div>
            
        </div>
    )
}

export default HeroSection;