import React from 'react';
import './Roadmap.css';
import { ReactComponent as AvaxIcon } from '../../assets/images/avax-logo.svg';
import { ReactComponent as CheckmarkIcon } from '../../assets/images/white-checkmark.svg';
import RoadmapElements from './RoadmapElements';
import { VerticalTimeline as VerticalRoadmap, VerticalTimelineElement as VerticalRoadmapElements } from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";



const Roadmap = () => {

    let checkmarkIconStyles = { background: "#e84142" };
    let avaxIconStyles = { background: "#fff" };

    return (
        <div className='roadmap-container' id='roadmap'>
            <div className='roadmap-wrapper'>
            <h1>Roadmap</h1>

            <VerticalRoadmap >
                {
                    RoadmapElements.map(element => {

                        let isCheckmarkIcon = element.icon === "checkmark";
                        let showButton = element.buttonText !== undefined && element.buttonText !== null && element.buttonText !== "";

                        return (
                            <VerticalRoadmapElements
                                key={element.key}
                                date={element.date}
                                dateClassName="date"
                                iconStyle={ isCheckmarkIcon ? checkmarkIconStyles : avaxIconStyles }
                                icon={ isCheckmarkIcon ? <CheckmarkIcon /> : <AvaxIcon /> }
                            >

                                <h3 className="vertical-timeline-element-title">
                                    {element.title}
                                </h3>

                                <h5 className="vertical-timeline-element-subtitle">
                                    {element.location}
                                </h5>

                                <p id="description">{element.description}</p>

                                {showButton && (
                                    <a
                                    className={`button ${
                                        isCheckmarkIcon ? "checkmarkButton" : "avaxButton"
                                    }`}
                                    href="/"
                                    >
                                    {element.buttonText}
                                    </a>
                                )}

                            </VerticalRoadmapElements>
                        );
                    })
                }
            </VerticalRoadmap>
            </div>
        </div>
    )
}

export default Roadmap;
