import React, {useState, useEffect} from 'react'
import {FaBars} from 'react-icons/fa'
import {Nav, NavbarContainer, NavLogo, MobileIcon, NavMenu, NavItem, NavLinks, NavBtn, NavBtnLink } from './NavbarElements';
import { IconContext } from 'react-icons/lib';
import {animateScroll} from 'react-scroll';

const Navbar = ({toggle}) => {
    const [scrollNav, setScrollNav] = useState(false);

    const changeNav = () => {
        if(window.scrollY >= 80) {
            setScrollNav(true)
        } else {
            setScrollNav(false)
        }
    }

    const toggleHome = () => {
        animateScroll.scrollToTop()
    }

    useEffect(() => {
        window.addEventListener('scroll', changeNav) 
    },[])

    return (
        <>
        <IconContext.Provider value={{color: '#fff'}}>
            <Nav scrollNav={scrollNav}>
                <NavbarContainer>
                    <NavLogo to='/' onClick={toggleHome}>
                        paracosms
                    </NavLogo>

                    <MobileIcon onClick={toggle}>
                        <FaBars />
                    </MobileIcon>

        {/* in non styled components, add activeClass='active' in navlinks and then in CSS file, create .active class and change color or whatever i want to add*/}
                    <NavMenu>
                        

                        <NavItem>
                            <NavLinks to='mint' smooth={true} duration={500} spy={true} exact='true' offset={-80} >Mint</NavLinks>
                        </NavItem>

                        <NavItem>
                            <NavLinks to='about'
                            smooth={true} 
                            duration={500} 
                            spy={true} 
                            exact='true' 
                            offset={-80} 
                            >About</NavLinks>
                        </NavItem>

                        <NavItem>
                            <NavLinks to='briefing' smooth={true} duration={500} spy={true} exact='true' offset={-80} >Briefing</NavLinks>
                        </NavItem>

                        <NavItem>
                            <NavLinks to='roadmap' smooth={true} duration={500} spy={true} exact='true' offset={-80} >Roadmap</NavLinks>
                        </NavItem>

                        {/* <NavItem>
                            <NavLinks to='team' smooth={true} duration={500} spy={true} exact='true' offset={-80} >Team</NavLinks>
                        </NavItem> */}

                        <NavItem>
                            <NavLinks to='faq' smooth={true} duration={500} spy={true} exact='true' offset={-80} >FAQ</NavLinks>
                        </NavItem>
                    </NavMenu>

                    <NavBtn>
                        <NavBtnLink to='' smooth={true} duration={500} spy={true} exact='true' offset={-80} >Wallet Connected</NavBtnLink>
                    </NavBtn>
                       

                </NavbarContainer>
            </Nav>
        </IconContext.Provider>
        </>
    )
}

export default Navbar;
