import React, {useState, useEffect} from 'react';
import { Button } from '../ButtonElements';
import { InfoContainer, InfoWrapper, InfoRow, Column1, Column2, TextWrapper, TopLine, Heading, Subtitle, BtnWrap, ImgWrap, Img } from './MintElements';
import './Mint.css';
import {ethers} from 'ethers';
import '../WalletCardComponent/WalletCard.css';

import ParacosmsAbi from './Paracosms.json';

const PARACOSMS_ADDRESS = '0x92cDaB7C9b8b7dD9CC59F7d00DC01b7D1078670e';

const MintSection = () => {
    const [inputQuantity, setInputQuantity] = React.useState('');

    const [supply, setSupply] = React.useState(null);

    const [errorMessage, setErrorMessage] = React.useState(null);
	const [defaultAccount, setDefaultAccount] = useState(null);
	const [userBalance, setUserBalance] = useState(null);
	const [connButtonText, setConnButtonText] = useState('Connect Wallet');
	const [provider, setProvider] = useState(null);

    React.useEffect(() => {
        if (!provider) return;

        async function loadSupply() {
            const contract = new ethers.Contract(PARACOSMS_ADDRESS, ParacosmsAbi, provider);
        
            try {
                const totalSupply = await contract.totalSupply();
                setSupply(totalSupply);
            } catch (err) { 
                console.log(err);
            }
        }

        loadSupply();
    }, [provider]);

	const connectWalletHandler = () => {
		if (window.ethereum && defaultAccount == null) {
			// set ethers provider
			setProvider(new ethers.providers.Web3Provider(window.ethereum));

			// connect to metamask
			window.ethereum.request({ method: 'eth_requestAccounts'})
			.then(result => {
				setConnButtonText('Wallet Connected');
				setDefaultAccount(result[0]);
			})
			.catch(error => {
				setErrorMessage(error.message);
			});

		} else if (!window.ethereum){
			console.log('Need to install MetaMask');
			setErrorMessage('Please install MetaMask browser extension to interact');
		}
	}

    const onBuyClicked = async () => {
        if (!provider.getSigner()) {
            alert('Connect wallet');
            return;
        }

        if (inputQuantity === '') {
            alert('Enter a quantity');
            return;
        }

        if (userBalance !== null) {
            if (userBalance < 1.25 * inputQuantity) {
                alert('Insufficient AVAX balance. Price is 1.25 AVAX each.');
                return;
            }
        }

        const contract = new ethers.Contract(PARACOSMS_ADDRESS, ParacosmsAbi).connect(provider.getSigner());

        try {
            await contract.mintPresale(+inputQuantity, {value: ethers.utils.parseEther('1.25').mul(inputQuantity)});
        } catch (err) {
            // handle err
            console.log(err);
            if (err?.data?.message) {
                alert(err.data.message);
            } else {
                alert(err.message);
            }
        }
    };

    const onInputQuantityChange = (event) => {
        if (event.target.value === '') { 
            setInputQuantity('');
            return;
        }

        const quantity = parseInt(event.target.value);

        if (isNaN(quantity)) {
            return;
        }

        if (quantity <= 0 || quantity > 30) {
            return;
        }

        setInputQuantity(quantity + '');
    };

    const renderButton = () => {
        if (provider?.getSigner()) {
            return (
                <button className="mint-submit-btn" onClick={onBuyClicked}>
                Buy
            </button>
            );
        } else {
            return (
                <button className="mint-submit-btn" onClick={connectWalletHandler}>
                Connect Wallet
            </button>
            );
        }
    }

useEffect(() => {
	if(defaultAccount){
	provider.getBalance(defaultAccount)
	.then(balanceResult => {
		setUserBalance(ethers.utils.formatEther(balanceResult));
	})
	};
}, [defaultAccount]);

    return (
        <>
<button className="wallet-btn" onClick={connectWalletHandler}>{connButtonText}</button>

            <InfoContainer lightBg='true' id='mint'>
            {/* <button className="wallet-btn" onClick={connectWalletPressed}>
        {walletAddress.length > 0 ? (
          "Connected: " +
          String(walletAddress).substring(0, 6) +
          "..." +
          String(walletAddress).substring(38)
        ) : (
          <span>Connect Wallet</span>
        )}
        </button> */}

                <InfoWrapper>
                    <InfoRow imgStart='true'>
                        <Column1>
                            <TextWrapper>
                                <TopLine>
                                MINT YOUR PARACOSMS REALMS
                                </TopLine>

                                <Heading lightText='false'>
                                    PRESALE
                                </Heading>

                                <div className="mint-caption">
                                <p>Price: 1.75 AVAX. Total Supply: 8,888.</p>
                                    </div>

                                {/* <Subtitle darkText='true'>
                                    { supply ? supply.toString() : 'X' } / 300 already claimed!
                                </Subtitle> */}

                                <div className="mint-form-container">
                                    <div className="mint-form-wrapper">
                                    
                                        <div className="mint-input-wrapper">
                                            <p>Input Quantity:</p>
                                            <input className="mint-input" type="text" placeholder="Max 30 per txn" value={inputQuantity} onChange={onInputQuantityChange} />

                                            {renderButton()}
                                        </div>
                                    </div>
                                </div>

                                <div className="mint-caption">Every realm you hold will give you more points to partake in presales, surprise airdrops, and exclusive collections on the Paracosms launchpad.</div>
                                <div className="mint-caption">Images will be revealed in your portfolio on Christmas Day, 48 hours after the sale. Prior to the reveal, we will be debuting our artist profiles, partnerships, and more, steadily building our community as we go. Happy Holidays!</div>
                                {/* <BtnWrap>
                                    <Button to='/'
                                        smooth={true}
                                        duration={500}
                                        spy={true}
                                        exact='true'
                                        offset={-80}
                                        primary={primary ? 1 : 0}
                                        dark={dark ? 1 : 0}
                                        dark2={dark2 ? 1 : 0}
                                    >{buttonLabel}</Button>
                                </BtnWrap> */}
                            </TextWrapper>
                        </Column1>

                        <Column2>
                            <ImgWrap>
                                <Img src={require('../../assets/images/paracosms_gif_compressed2.gif').default} alt='gif of paracosm examples'/>
                            </ImgWrap>
                        </Column2>

                    </InfoRow>
                </InfoWrapper>
            </InfoContainer>

            {/* <div className='walletCard'>
		<h4> Connection to MetaMask using ethers.js </h4>
			
			<div className='accountDisplay'>
				<h3>Address: {defaultAccount}</h3>
			</div>
			<div className='balanceDisplay'>
				<h3>Balance: {userBalance}</h3>
			</div>
			{errorMessage}
		</div> */}
        </>
    )
}

export default MintSection;
