import React, {useState, useEffect} from 'react';
import Navbar from '../../components/Navbar';
import Sidebar from '../../components/Sidebar';
import HeroSection from '../../components/HeroSection2/HeroSection';
import MintSection from '../../components/MintSection';
import { mintSectionObj } from '../../components/MintSection/Data';
import TeamArchived from '../../components/Team_Archived';
import Footer from '../../components/Footer/Footer';
import About from '../../components/AboutSection/About';
import Roadmap from '../../components/RoadmapSection/Roadmap';
import Faq from '../../components/FaqSection/Faq';
import Cards from '../../components/CardSection/Cards';

const Home = () => {
    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => {
        setIsOpen(!isOpen);
    };

    useEffect(() => {
        window.scrollTo(0, 0);
      }, []);

    return (
        <>
            <Navbar toggle={toggle}/>
            <Sidebar isOpen={isOpen} toggle={toggle} />
            <HeroSection />
            <MintSection {...mintSectionObj}/>
            <About />
            <Cards />
            <Roadmap />
            {/* <TeamArchived /> */}
            <Faq />
            <Footer />
        </>
    );
};

export default Home;
